.login-bg {
    background-position: center center !important;
    background-repeat: no-repeat;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    background-color: #f4f5f8!important;
}

.login-page {
    margin-top: 5vh;
    max-width: 480px;
    margin-inline: auto;
    
    & .card {
        padding-top: 20px;
    }

    & input {
        padding: 0.8rem 1.8rem 0.6rem!important;
        line-height: 2.5em!important;
        color: #565656!important;
        margin-top: -2px;

        &:focus {
            border-color: #ddd;
        }
    }

    & button {
        display: block;
        width: 100%;
        padding: 0.8rem 1.8rem 0.6rem!important;
        line-height: 2.5em!important;
        text-transform: uppercase;
    }
    & h3 {
        color: #999;
    }
}